export default {
	data() {
		return {
			show: false,
			shade: false,
			phone: '',
			pwd: '',
			pwd1: '',
			code: '',
			iscode: '', //用于存放验证码接口里获取到的code
			codename: ' 获取验证码 ',
			disabled: false,
			flag: true
		};
	},
	methods: {
		getPhoneValue: function(e) {
			this.phone = e.detail.value;
		},
		getPwdValue: function(e) {
			this.pwd = e.detail.value;
		},
		getPwdValue1: function(e) {
			this.pwd1 = e.detail.value;
		},
		getCodeValue: function(e) {
			this.code = e.detail.value;
		},
		getCode: function() {
			var _this = this;
			//判断手机号格式
			var myreg = /^(16[0-9]|14[0-9]|13[0-9]|15[0-9]|17[0-9]|18[0-9]|19[0-9])\d{8}$$/;
			if (this.phone == '') {
				uni.showToast({
					title: '手机号不能为空',
					icon: 'none',
					duration: 1000
				});
				return false;
			} else if (!myreg.test(this.phone)) {
				uni.showToast({
					title: '请输入正确的手机号',
					icon: 'none',
					duration: 1000
				});
				return false;
			} else {
				uni.request({
					//短信接口
					url: _this.url + 'users/regist/sms/',
					method: 'POST',
					data: {
						mobile: this.phone
					},
					header: {
						'Content-Type': 'application/x-www-form-urlencoded'
					},
					success(res) {
						//根据code判断
						console.log(res);
						var ocode = res.statusCode;
						if (ocode == 200) {
							_this.iscode = res.data.data;
							var num = 61;
							var timer = setInterval(function() {
								num--;
								if (num <= 0) {
									clearInterval(timer);
									(_this.codename = '重新发送'), (_this.disabled = false);
								} else {
									(_this.codename = num + 's'), (_this.disabled = true);
								}
							}, 1000);
						} else if (ocode == 411) {
							uni.showToast({
								title: '操作太频繁，请稍候重试',
								icon: 'none',
								duration: 2000
							});
							return false;
						} else if (ocode == 400) {
							uni.showToast({
								title: '手机号已注册',
								icon: 'none',
								duration: 2000
							});
							return false;
						}
					}
				});
			}
		},
		//获取验证码
		getCodeBtn: function(e) {
			this.getCode();
		},
		register() {
			var _self = this;
			if (this.phone == '') {
				uni.showToast({
					title: '请输入手机号',
					icon: 'none',
					duration: 2000
				});
				return false;
			}
			var myreg = /^(16[0-9]|14[0-9]|13[0-9]|15[0-9]|17[0-9]|18[0-9]|19[0-9])\d{8}$$/;
			if (!myreg.test(this.phone)) {
				uni.showToast({
					title: '请输入正确的手机号',
					icon: 'none',
					duration: 2000
				});
				return false;
			}
			if (this.pwd == '') {
				uni.showToast({
					title: '请输入登录密码',
					icon: 'none',
					duration: 2000
				});
				return false;
			}
			var str = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,16}$/;
			if (!str.test(this.pwd)) {
				uni.showToast({
					title: '密码格式不正确',
					icon: 'none',
					duration: 2000
				});
				return false;
			}
			if (this.pwd1 == '') {
				uni.showToast({
					title: '请确认登录密码',
					icon: 'none',
					duration: 2000
				});
				return false;
			}
			if (this.pwd1 !== this.pwd) {
				uni.showToast({
					title: '两次密码不一致',
					icon: 'none',
					duration: 2000
				});
				return false;
			}
			if (this.code == '') {
				uni.showToast({
					title: '验证码不能为空',
					icon: 'none',
					duration: 2000
				});
				return false;
			}
			if (this.code != this.iscode) {
				uni.showToast({
					title: '验证码错误',
					icon: 'none',
					duration: 2000
				});
				return false;
			}
			uni.request({
				url: this.url + 'users/regist/',
				method: 'POST',
				data: {
					mobile: this.phone,
					password: this.pwd,
					sec_password: this.pwd1,
					code: this.code
				},
				headers: {
					'Content-Type': 'application/json'
				},
				success: res => {
					_self.global_.phone = this.phone;
					_self.global_.token = res.data.token;
					if (res.statusCode == 400) {
						uni.showToast({
							title: '验证码已过期',
							icon: 'none'
						});
					}
					if (res.statusCode == 201) {
						this.shade = true;
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		sure: function() {
			uni.navigateTo({
				url: '../login/login'
			});
		}
	}
};
